  display: flex !important;
  align-items: center;
  height: 48px;
  padding-left: 5px;
}

.container-inner {
  padding: 20px;
  padding-bottom: 20px;
  width: 100%;
  max-width: 880px;
  min-width: 290px;
}

.container-and-sidebar {
